<template>
    <div>
        <!-- 头部 -->
       <header>
           <div class="head">
               <div class="h-left">
                   <span><img :src="img1" ></span>
               </div>
               <div class="h-right" >
                   <span  v-for="(item,index) in right" :key="index" >{{item}}</span>
               </div>
           </div>
       </header>
       <!-- 轮播 -->
       <section class="carousel">
           <mt-swipe :auto="10000">
                <mt-swipe-item v-for="(item,index) in img2" :key="index">
                    <img :src="item" alt="">
                </mt-swipe-item>
                
            </mt-swipe>
            <div class="zhong">
            <span class="s1" v-for="(item,index) in text9" :key="index">{{item.name1}}</span>
            <span v-for="(item,index) in text9" :key="index">{{item.name}}</span>
            </div>
       </section>
      
       <!-- 提供服务 -->
        <section class="service">
            <div class="s-background" :style="background">
                <p>{{text}}</p>
                <div class="s-include" >
                    <div class="s-2" v-for="(item,index) in img3 " :key="index" >
                        <span><img :src="item.name" alt="">
                            <span>{{item.text}}</span>
                            <img class="s-img" :src="item.name2" alt="">
                        </span>
                         
                    </div> 
                </div>
            </div>
        </section>
       <!-- 新产品推荐 -->
        <section class="product" >
            <p>{{text2}}</p>
            <div class="p-background" :style="background2">
                <div class="p-product" v-for="(item,index) in img4" :key="index">
                    <span><img :src="item.name" alt=""></span>
                </div>
            </div>
        </section>
       <!-- 行业成功案例 -->
        <section class="case">
            <p>{{text3}}</p>
            <div class="c-background" :style="background3">
                <div class="c-left">
                    <span v-for="(item,index) in img5" :key="index">
                        <img :src="item.name" alt="">
                    </span>
                </div>
                <div class="c-right">
                    <img :src="img6" alt="">
                </div>
            </div>
        </section>
       <!-- 关于我们 -->
        <section class="about">
            <div class="a-left">
                <p>{{text4}}</p>
                <img :src="img7" alt="">
            </div>
            <div class="a-right">
                <span class="s1">
                    <p class="p1">{{text5}}</p>
                    <p class="p2">{{text6}}</p>
                </span>
                <span class="s2">
                    <p class="p2">{{text8}}</p>
                </span>
                <img :src="img8" alt="">
            </div>
        </section>
       <!-- 版权 -->
        <section class="copyright">
            <div class="c-top">
                <div class="t-left">
                    <tr>
                        <th  class="list1">
                            <ul>
                                <span v-for="(item,index) in text13" :key="index">{{item.name1}}</span>
                                <li v-for="(item,index) in img9" :key="index"><img :src="item.name" alt="">{{item.text}}</li>
                            </ul>
                        </th>
                        <th  class="list2">
                            <ul>
                                <span v-for="(item,index) in text13" :key="index">{{item.name2}}</span>
                                <p  v-for="(item,index) in text13" :key="index">{{item.name3}}</p>
                                <img :src="img10" alt="">
                                <p class="p1">北京畅哲公众号</p>
                            </ul>
                        </th>
                    </tr>
                   
                </div>
                <div class="t-right">
                    <div class="r1">
                        <p>获得免费账号</p>
                    <input type="text" name="" placeholder="公司名称（必填）">
                    <input type="text" name="" placeholder="联系人（必填）">
                    <input type="text" name="" placeholder="Email（必填）">
                    <input type="text" name="" placeholder="联系电话（必填）">
                    <input type="text" name="" placeholder="微信（必填）">
                    </div>
                    
                    <div class="r2">
                    <input type="submit" value="发送" >
                    </div>


                </div>
            </div>
            <div class="c-bottom">
                <p>@2015-2019北京畅哲科技有限公司&nbsp;&nbsp;备案号&nbsp;&nbsp;京ICP备18025918</p>
                <p><img :src="img11" alt="">北京畅哲科技有限公司</p>
            </div>
        </section>
    </div>
</template>

<script>
// 导入lodash插件
import _ from 'lodash';
// 引入Mint ui全部组件
import Vue from 'vue';
import Mint from 'mint-ui';
//轮播组件
import {Swipe,SwipeItem} from 'mint-ui';
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.use(Mint);
import 'mint-ui/lib/style.css'
import 'mint-ui/lib/index.js'
export default {
    data(){
        return{
            right:['首页','产品','解决方案','技术服务','成功案例','关于我们'],
            text:'我们提供的服务',
            text2:'新产品推荐',
            text3:'行业成功应用案例',
            text4:'关于我们',
            text5:'公司',
            text6:'简介',
            text8:'北京畅哲科技有限公司（CHARGERS），是由多名具有丰富经验的软件工程师组建的一家以研发为主的公司。',
            text9:[
                {name1:'首页'},
                {name:'服务'},
                {name:'产品'},
                {name:'联系'},
                
                ],
            text13:[
                {name1:'应用下载'},
                {name2:'联系我们'},
                {name3:'售后服务：'},
            ],
            background:{
                backgroundImage: 'url(' + require('../../images/images/222切片2_02.png') + ')'
            },
            background2:{
                backgroundImage: 'url(' + require('../../images/images/222切片2_05.png') + ')'
            },
            background3:{
                backgroundImage: 'url(' + require('../../images/images/222切片2_07.png') + ')'
            },
            img1:require("@/images/images/112345_03.gif"),
            img2:[require("@/images/images/112345_07.png"),require("@/images/images/112345_07.png"),require("@/images/images/112345_07.png"),require("@/images/images/112345_07.png"),require("@/images/images/112345_07.png")],
            img3:[  {name:require("@/images/images/112345_11.gif"),text:'疑问服务',name2:require("@/images/images/3334_07.gif")},
                    {name:require("@/images/images/3334_03.gif"),text:'记录服务',name2:require("@/images/images/3334_08.gif")},
                    {name:require("@/images/images/112345_13.gif"),text:'系统集成',name2:require("@/images/images/3334_08.gif")},
                    {name:require("@/images/images/112345_15.gif"),text:'落地实施',name2:require("@/images/images/3334_09.gif")},
                    {name:require("@/images/images/112345_17.gif"),text:'运营服务',name2:require("@/images/images/3334_10.gif")},
            ],
            img4:[
                {name:require('@/images/images/112345_24.gif')},
                {name:require('@/images/images/112345_30.gif')},
                {name:require('@/images/images/112345_27.gif')},
                {name:require('@/images/images/112345_36.gif')},
                {name:require('@/images/images/112345_37.gif')},
                {name:require('@/images/images/112345_39.gif')},
            ],
            img5:[
                {name:require('@/images/images/112345_44.gif')},
                {name:require('@/images/images/112345_46.gif')},
                {name:require('@/images/images/112345_48.gif')},
                {name:require('@/images/images/112345_56.gif')},
                {name:require('@/images/images/112345_58.gif')},
                {name:require('@/images/images/112345_59.gif')},
            ],
            img6:require('@/images/images/112345_51.gif'),
            img7:require('@/images/images/112345_66.gif'),
            img8:require('@/images/images/112345_70.gif'),
            img9:[
                {name:require('@/images/images/112345_77.gif'),text:'IOS'},
                {name:require('@/images/images/112345_80.gif'),text:'Android'},
                {name:require('@/images/images/112345_82.gif'),text:'Windows'},
                {name:require('@/images/images/112345_85.gif'),text:'MAC'},
                {name:require('@/images/images/112345_87.gif'),text:'WinXP专企版'},
            ],
            img10:require('@/images/images/112345_74.gif'),
            img11:require('@/images/images/112345_86.png')
        }
    },
   
}
</script>


<style lang="less" scoped>
    .head{
        width:100%;
        display: flex;
        align-items: center;
        img{
            margin-left:5%;
            width:2.5rem;
            height:.625rem;
        }
        .h-right{
            width: 100%;
             display: flex;
             justify-content: space-around;
             
            
        }
        span{
            margin-left: 5px;
            font-size:10px;
        }
    }
    //轮播
    .carousel{
        width: 100%;
        height: 140px;
        font-size: 0;
        padding: 0;
        margin: 0;
        img{
            width:100%;
            height: 100%;
            position: absolute;
        }
        .zhong{
            display: flex;
            flex-wrap: nowrap;
            margin-top: -7px;
            margin-left: 120px;
            position: relative;
            width:120px;
            height:15px;
            background-color: #ffffff;
             .s1{    
                 background-color: #f1df39;
                }
            span{
                height: 100%;
                text-indent:8px;
                font-size: 10px;
            }
        }
        
    }
   
    //服务
    .service{
        width: 100%;
        height: 130px;
        margin-top: 20px;
        .s-background{
            width:100%;
            height: 95%;
            background-size: contain;
            text-align: center;
            p{
                font-size:.3125rem;
                width:27%;
                margin: auto;
                padding-bottom: 5px;
                border-bottom: 1px solid #f89;
            }
            
        }
        .s-include{
            display: flex;
            justify-content: center;
            .s-2{
                display: flex;
                justify-content: center;
                margin-top:5px;
                width: 60px;
                height: 90px;
                background-color: aliceblue;
               
                span{
                    display: flex;
                    align-items: center;
                    flex-direction: column;  
                    height:60px;
                    margin-top: 13px;
                    font-size: 10px;
                    img{
                        width:80%;
                        height: 80%;
                    }
                    .s-img{
                        margin-top: 15px;
                        width:100%;
                        height:2px;
                    }
                }
                
            }
        }
    }
    //产品
    .product{
        height: 160px;
             p{
                height: 20px;
                width:16%;
                text-align: center;
                padding-top:3%;
                margin: auto;
                font-size:11px;
                border-bottom: 1px solid #f89;
            }
            .p-background{
                width:100%;
                height:135px;
                margin-top: 5px;
                background-size: contain;
                text-align: center;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: center;
                img{
                    margin-left: 10px;
                    width: 85px;
                    height:60px;
                }
            }
    }
    //案例
    .case{
        p{
            height: 20px;
            width:90px;
            text-align: center;
            padding-top:3%;
            margin: auto;
            font-size:11px;
            border-bottom: 1px solid #f89;
        }
        .c-background{
           margin-top: 5px;
            width:100%;
            height:130px;
            background-size: contain;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: space-around;
            .c-left{
                width:50%;
                img{
                margin-left: 5px;
                width: 50px;
                height: 50px;
            }
            }
            .c-right{
                width: 50%;
                img{
                width:95%;
                height:100%;
            }
            }
            
        }
    }
    //关于我们
    .about{
        height:130px;
        display: flex;
        align-items: center;
        background-color: aliceblue;
        .a-left{
            width:50%;
            display: flex;
            flex-flow: column;
            align-items: center;
            
            p{
                font-size:11px;
                border-bottom: 1px solid #f89;
                height: 20px;
                font-size:13px;
            }
            img{
                width: 80%;
                height: 100%;
            }
        }
        .a-right{
            width:50%;
            height: 100%;
            position: relative;
            background-color: rgba(226, 224, 222, 0.849);
            .s1{
                display: flex;
                .p1{
                    text-indent: 20px;
                    font-size: 13px;
                    color: rgb(245, 209, 9);
                    
                }
                .p2{
                    line-height: 10px;
                    font-size: 14px;
                }
                
            }
            .s2{
                p{
                    margin-top:-5px;
                    text-indent:5%;
                    font-size: 10px;
                }
                
            }
            img{
                width: 90%;
                height: 25%;
                position: absolute;
                bottom: 5px;
            }
        }
    }
    //应用下载
    .copyright{
        width: 100%;
        background-color:black;
        position: absolute;
        .c-top{
            width: 100%;
            height: 130px;
            position: relative;
            display: flex;
            flex-direction: row;
            .t-left{
                width:50%;
                span{
                    font-size: 15px;
                }
                img{
                    width:14px;
                    height:14px;
                }
                ul{
                    padding: 0;
                    margin:0;
                    color: rgb(184, 188, 190);
                    li{
                        width:70px;
                        height: 15px;
                        font-size: 5px;
                        list-style: none;
                        color: rgb(114, 112, 112);

                    }
                }
            
                tr{
                    .list1{
                        padding-left: 15px;
                        text-align: left;
                    }
                    .list2{
                        
                        p{
                            width: 90px;
                            font-size: 12px;
                            color: rgb(114, 112, 112);
                        }
                        .p1{
                            margin-left: 30%;
                            margin-top: -5px;
                            font-size: 1px;
                            color: rgb(184, 188, 190);
                        }
                         img{
                            margin-left:60%;
                            width: 35px;
                            height: 35px;
                        }
                    }
                }
            }
            .t-right{
                width: 50%;
                margin-top:9px;
                color: rgb(255, 255, 255);
                font-size: 10px;
                margin-left:50px;
                .r1{
                    input{
                        border: 0;
                        font-size: 10px;
                        border-radius: 9px;
                        background-color: #ece8e8;
                        display: block;
                        margin-top:5px;
                        width: 90px;
                        height:8px;
                        text-indent:5px;
                    }
                
                }
                .r2{
                  input{
                      margin-top:4px;
                      margin-left: 5px;
                      border: 0;
                      width: 30px;
                      height: 10px;
                      font-size:5px;
                      background-color: #f1df39;
                  }
                   
                }
            }
        }
        .c-bottom{
            position: relative;
            width: 100%;
            height: 100%;
            p{
                padding-top:2%;
                margin: auto;
                text-align: center;
                color: rgb(201, 197, 197);
                font-size: 5px;
            }
            img{
                width:10px;
                height: 10px;
            }
        }
    }
    

</style>





